.products {
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 88px;
}

.products__title {
  margin-top: 0;
  margin-bottom: 52px;

  font-family: $roboto;
  font-size: 36px;
  font-weight: normal;
  line-height: 51px;

  color: $white;
}

.products__list {
  @include list-reset;

  margin-left: -2.5%;
  padding-bottom: 105px;

  display: flex;
  flex-wrap: wrap;
}

.products__item {
  position: relative;
  box-sizing: border-box;

  width: 30.83%;
  min-height: 620px;
  margin-left: 2.5%;
  margin-bottom: 23px;

  transition-duration: 0.3s;

  &:hover,
  &:focus {
    box-shadow: 0 6px 10px rgba(0, 178, 255, 1);
    transition-duration: 0.3s;
  }

  &:active {
    box-shadow: 0 6px 10px rgba(0, 178, 255, 0.5);
    transition-duration: 0.3s;
  }

  &::after {
    content: "";

    position: absolute;
    bottom: 0;
    right: 0;

    width: 63px;
    height: 63px;

    background-color: $main-color;
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}

.products__link {
  position: relative;

  display: block;
  width: 100%;
  height: 100%;

  font-family: $roboto;
  font-size: 20px;
  line-height: 30px;
  color: $white;

  background-color: #27353d;

  p {
    margin: 0;
    padding-top: 52px;
    padding-left: 45px;
    padding-right: 160px;
  }

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    opacity: 0.2;
    background-size: cover;
  }

  &--production {
    &::before {
      background-image: url("../img/production-desktop@1x.jpg");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/production-desktop@2x.jpg");
      }
    }
  }

  &--mounting {
    &::before {
      background-image: url("../img/mounting-desktop@1x.jpg");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/mounting-desktop@2x.jpg");
      }
    }
  }

  &--companent {
    &::before {
      background-image: url("../img/companent-desktop@1x.jpg");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/companent-desktop@2x.jpg");
      }
    }
  }
}

@media (max-width: $tablet-width) {
  .products {
    padding: 0 3.1%;
    padding-top: 49px;
  }

  .products__title {
    margin-bottom: 48px;
    padding-right: 100px;

    font-size: 32px;
    line-height: 45px;
  }

  .products__list {
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 32px;
    flex-direction: column;
  }

  .products__item {
    width: 100%;
    min-height: 256px;
    margin-bottom: 46px;
    margin-left: 0;
  }

  .products__link {
    height: 256px;

    p {
      width: 24%;
      padding-top: 40px;
    }

    &--production {
      &::before {
        background-image: url("../img/production-tablet@1x.jpg");

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/production-tablet@2x.jpg");
        }
      }
    }

    &--mounting {
      &::before {
        background-image: url("../img/mounting-tablet@1x.jpg");

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/mounting-tablet@2x.jpg");
        }
      }
    }

    &--companent {
      p {
        padding-top: 50px;
      }

      &::before {
        background-image: url("../img/companent-tablet@1x.jpg");

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/companent-tablet@2x.jpg");
        }
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .products {
    padding-left: 4.5%;
    padding-right: 4.5%;
  }

  .products__list {
    padding-bottom: 34px;
  }

  .products__title {
    margin-bottom: 43px;
    padding-right: 0;
    padding-left: 10px;

    font-size: 28px;
    line-height: 39px;
  }

  .products__item {
    min-height: 256px;
    margin-bottom: 30px;
  }

  .products__link {
    p {
      width: 70%;
      padding-top: 20px;
      padding-left: 35px;
      padding-right: 0;
      box-sizing: border-box;
    }

    &--production {
      &::before {
        background-image: url("../img/production-mobile@1x.jpg");

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/production-mobile@2x.jpg");
        }
      }
    }

    &--mounting {
      &::before {
        background-image: url("../img/mounting-mobile@1x.jpg");

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/mounting-mobile@2x.jpg");
        }
      }
    }

    &--companent {
      p {
        padding-top: 30px;
      }

      &::before {
        background-image: url("../img/companent-mobile@1x.jpg");

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/companent-mobile@2x.jpg");
        }
      }
    }
  }
}
